<template>
  <div class="table-container">
    <h3 class="title">{{title}}</h3>
    <el-table
      :data="tableData"
      style="width: 100%"
    >
      <el-table-column
        v-for="(column, index) in columnData"
        :key="index"
        :prop="column.prop"
        :label="column.label"
        :width="column.width"
      >
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "LComponentDemo",
  props: {
    title: {
      type: String,
      default: "Attributes"
    },
    columnData: {
      type: Array
    },
    tableData: {
      type: Array
    }
  },
  data() {
    return {};
  }
};
</script>
<style scoped>
.table-container {
  user-select: text;
}
.title {
  font-weight: 400;
  color: #1f2f3d;
  margin: 55px 0 20px;
  font-size: 22px;
  text-align: left;
}
</style>



