<template>
  <div>
    <el-row class="main">
      <el-col
        :span="24"
        class="title"
      >滑块验证</el-col>
      <el-col
        :span="24"
        class="subtitle"
      >基于vue-drag-verify二次开发的vue组件。需对父元素或html设置<b>user-select: none</b></el-col>
      <el-col :span="24">
        <l-component-demo
          title="基础用法"
          subtitle="基础的滑块用法。"
          :code="buttonCode1"
        >
          <div slot="source">
            <el-row>
              <drag-verify
                ref="dragVerify"
                :isPassing.sync="isPassing1"
                handlerIcon="el-icon-d-arrow-right"
                successIcon="el-icon-circle-check"
              >
              </drag-verify>
            </el-row>
            <el-row style="margin-top:10px">
              <drag-verify
                ref="dragVerify"
                :isPassing.sync="isPassing2"
                text="请按住滑块拖动"
                successText="验证通过"
                handlerIcon="el-icon-d-arrow-right"
                successIcon="el-icon-circle-check"
              >
              </drag-verify>
            </el-row>
            <el-row style="margin-top:10px;">
              <drag-verify
                ref="dragVerify"
                :isPassing.sync="isPassing3"
                text="请按住滑块拖动"
                successText="验证通过"
                handlerIcon="el-icon-arrow-right"
                completedBg="#93a6ff"
                successIcon="el-icon-check"
              >
              </drag-verify>
            </el-row>
            <el-row style="margin-top:10px;">
              <drag-verify
                ref="dragVerify"
                :isPassing.sync="isPassing4"
                :circle="true"
                text="请按住滑块拖动"
                successText="验证通过"
                handlerIcon="el-icon-d-arrow-right"
                successIcon="el-icon-circle-check"
              >
              </drag-verify>
            </el-row>
            <el-row style="margin-top:10px;">
              <drag-verify
                ref="dragVerify"
                :width="300"
                :isPassing.sync="isPassing5"
                text="请按住滑块拖动"
                successText="验证通过"
                background="#ccc"
                completedBg="rgb(105, 231, 251)"
                handlerIcon="el-icon-d-arrow-right"
                successIcon="el-icon-circle-check"
                @passcallback="passcallback"
              >
              </drag-verify>
            </el-row>
          </div>
          <div slot="description">
            使用<code>width</code>、<code>height</code>、<code>text</code>、<code>background</code>和<code>textColor</code>等属性调整组件。
          </div>
        </l-component-demo>
        <l-component-demo
          title="插槽用法"
          subtitle="扩展提示信息。"
          :code="buttonCode2"
        >
          <div slot="source">
            <el-row style="margin-top:10px;">
              <drag-verify
                ref="dragVerify"
                :width="300"
                :isPassing.sync="isPassing6"
                text="请按住滑块拖动"
                successText="验证通过"
                handlerIcon="el-icon-d-arrow-right"
                successIcon="el-icon-circle-check"
                @passcallback="passcallback2"
              >
                <i
                  v-show="!isPassing6"
                  slot="textBefore"
                  class="el-icon-lock"
                ></i>
              </drag-verify>
            </el-row>
          </div>
          <div slot="description">
            使用<code>textBefore</code>、<code>textAfter</code>插槽扩展提示信息。
          </div>
        </l-component-demo>
        <l-component-demo
          title="状态还原"
          subtitle="还原未验证通过状态。"
          :code="buttonCode3"
        >
          <div slot="source">
            <el-row style="margin-top:10px;">
              <drag-verify
                ref="dragVerify7"
                :width="300"
                :isPassing.sync="isPassing7"
                text="请按住滑块拖动"
                successText="验证通过"
                handlerIcon="el-icon-d-arrow-right"
                successIcon="el-icon-circle-check"
                @passcallback="passcallback3"
              >
              </drag-verify>
              <el-button
                style="margin-top:5px;"
                size="small"
                @click="reset"
              >还原</el-button>
            </el-row>
          </div>
          <div slot="description">
            使用<code>reset</code>方法还原状态。
          </div>
        </l-component-demo>
      </el-col>
      <el-col :span="24">
        <l-component-note
          title="Attributes"
          :columnData="columnData1"
          :tableData="tableData1"
        ></l-component-note>
      </el-col>
      <el-col :span="24">
        <l-component-note
          title="Slots"
          :columnData="columnData2"
          :tableData="tableData2"
        ></l-component-note>
      </el-col>
      <el-col :span="24">
        <l-component-note
          title="Methods"
          :columnData="columnData3"
          :tableData="tableData3"
        ></l-component-note>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import LComponentNote from "@/components/common/LComponentNote";
import LComponentDemo from "@/components/common/LComponentDemo";
import dragVerify from "@/components/core/dragVerify";
export default {
  name: "DragComponentApi",
  components: {
    LComponentNote,
    LComponentDemo,
    dragVerify
  },
  methods: {
    passcallback() {
      this.$message({
        message: "验证通过",
        type: "success"
      });
    },
    passcallback2() {
      this.$message({
        message: "验证通过,插槽消失",
        type: "success"
      });
    },
    passcallback3() {
      this.$message({
        message: "验证通过",
        type: "success"
      });
    },
    reset() {
      this.isPassing7 = false;
      this.$refs.dragVerify7.reset();
    }
  },
  data() {
    return {
      isPassing1: false,
      isPassing2: false,
      isPassing3: false,
      isPassing4: false,
      isPassing5: false,
      isPassing6: false,
      isPassing7: false,
      buttonCode1: `<el-row>
  <drag-verify
    ref="dragVerify"
    :isPassing.sync="isPassing1"
    handlerIcon="el-icon-d-arrow-right"
    successIcon="el-icon-circle-check"
  >
  </drag-verify>
</el-row>
<el-row style="margin-top:10px">
  <drag-verify
    ref="dragVerify"
    :isPassing.sync="isPassing2"
    text="请按住滑块拖动"
    successText="验证通过"
    handlerIcon="el-icon-d-arrow-right"
    successIcon="el-icon-circle-check"
  >
  </drag-verify>
</el-row>
<el-row style="margin-top:10px;">
  <drag-verify
    ref="dragVerify"
    :isPassing.sync="isPassing3"
    completedBg="#93a6ff"
    text="请按住滑块拖动"
    successText="验证通过"
    handlerIcon="el-icon-arrow-right"
    successIcon="el-icon-check"
  >
  </drag-verify>
</el-row>
<el-row style="margin-top:10px;">
  <drag-verify
    ref="dragVerify"
    :isPassing.sync="isPassing4"
    :circle="true"
    text="请按住滑块拖动"
    successText="验证通过"
    handlerIcon="el-icon-d-arrow-right"
    successIcon="el-icon-circle-check"
  >
  </drag-verify>
</el-row>
<el-row style="margin-top:10px;">
  <drag-verify
    ref="dragVerify"
    :width="300"
    :isPassing.sync="isPassing5"
    text="请按住滑块拖动"
    successText="验证通过"
    background="#ccc"
    completedBg="rgb(105, 231, 251)"
    handlerIcon="el-icon-d-arrow-right"
    successIcon="el-icon-circle-check"
    @passcallback="passcallback"
  >
  </drag-verify>
</el-row>
`,
      buttonCode2:
        `
  <el-row style="margin-top:10px;">
    <drag-verify
      ref="dragVerify"
      :width="300"
      :isPassing.sync="isPassing6"
      text="请按住滑块拖动"
      successText="验证通过"
      handlerIcon="el-icon-d-arrow-right"
      successIcon="el-icon-circle-check"
      @passcallback="passcallback2"
    >
      <i
        v-show="!isPassing6"
        slot="textBefore"
        class="el-icon-lock"
      ></i>
    </drag-verify>
  </el-row>
  
 <script>
    export default{
      data:(){
        return{
          isPassing6: false
        }
      },
      methods:{
        passcallback2() {
          this.$message({
            message: "验证通过,插槽消失",
            type: "success"
          });
        },
      }
    }
  <` + "/script>",
      buttonCode3:
        `
  <el-row style="margin-top:10px;">
    <drag-verify
      ref="dragVerify7"
      :width="300"
      :isPassing.sync="isPassing7"
      text="请按住滑块拖动"
      successText="验证通过"
      handlerIcon="el-icon-d-arrow-right"
      successIcon="el-icon-circle-check"
      @passcallback="passcallback3"
    >
    </drag-verify>
    <el-button
      style="margin-top:5px;"
      size="small"
      @click="reset"
    >还原</el-button>
  </el-row>
  <script>
      export default{
        data:(){
          return{
            isPassing7: false
          }
        },
        methods:{
          passcallback3() {
            this.$message({
              message: "验证通过",
              type: "success"
            });
          },
          reset() {
            this.isPassing7 = false;
            this.$refs.dragVerify7.reset();
          },
        }
      }
    <` + "/script>",
      columnData1: [
        {
          prop: "attribute",
          label: "参数",
          width: "120"
        },
        {
          prop: "desc",
          label: "说明",
          width: "200"
        },
        {
          prop: "type",
          label: "类型",
          width: "80"
        },
        {
          prop: "value",
          label: "可选值"
        },
        {
          prop: "default",
          label: "默认值",
          width: "100"
        }
      ],
      tableData1: [
        {
          attribute: "width",
          desc: "宽度",
          type: "number",
          value: "-",
          default: "250"
        },
        {
          attribute: "height",
          desc: "高度",
          type: "number",
          value: "-",
          default: "40"
        },
        {
          attribute: "text",
          desc: "初始文字",
          type: "string",
          value: "-",
          default: "swiping to the right side"
        },
        {
          attribute: "successText",
          desc: "成功提示文字",
          type: "string",
          value: "-",
          default: "success"
        },
        {
          attribute: "background",
          desc: "滑块右侧背景色",
          type: "string",
          value: "#eee / red / rgba(52,52,52,0.7)",
          default: "#eee"
        },
        {
          attribute: "progressBarBg",
          desc: "滑块左侧背景色",
          type: "string",
          value: "#76c61d / white / rgba(52,52,52,0.7)",
          default: "#76c61d"
        },
        {
          attribute: "handlerBg",
          desc: "滑块背景色",
          type: "string",
          value: "#fff / white / rgb(255,255,255)",
          default: "#fff"
        },
        {
          attribute: "completedBg",
          desc: "验证通过背景色",
          type: "string",
          value: "#76c61d / white / rgba(52,52,52,0.7)",
          default: "#76c61d"
        },
        {
          attribute: "circle",
          desc: "两侧是否圆形",
          type: "boolean",
          value: "true / false",
          default: "false"
        },
        {
          attribute: "radius",
          desc: "圆角",
          type: "string",
          value: "4px / 4%",
          default: "4px"
        },
        {
          attribute: "handlerIcon",
          desc: "滑块未验证通过时的图标,根据所选框架设置不同class",
          type: "string",
          value: "el-icon-d-arrow-right",
          default: "-"
        },
        {
          attribute: "successIcon",
          desc: "滑块验证通过时的图标,根据所选框架设置不同class",
          type: "string",
          value: "el-icon-circle-check",
          default: "-"
        },
        {
          attribute: "textSize",
          desc: "文字大小",
          type: "string",
          value: "14px / 4em",
          default: "14px"
        },
        {
          attribute: "textColor",
          desc: "文字颜色",
          type: "string",
          value: "#333 / gray / rgb(52,52,52)",
          default: "#333"
        }
      ],
      columnData2: [
        {
          prop: "name",
          label: "name",
          width: "120"
        },
        {
          prop: "说明",
          label: "说明"
        }
      ],
      tableData2: [
        {
          name: "textBefore",
          说明: "提示文字前"
        },
        {
          name: "textAfter",
          说明: "提示文字后"
        }
      ],
      columnData3: [
        {
          prop: "方法名",
          label: "方法名",
          width: "120"
        },
        {
          prop: "说明",
          label: "说明"
        },
        {
          prop: "参数",
          label: "参数",
          width: "120"
        }
      ],
      tableData3: [
        {
          方法名: "reset",
          说明: "还原未验证通过时的状态",
          参数: "-"
        }
      ]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main {
  width: 1000px;
  margin: 0 auto;
  padding: 30px 40px;
  border-radius: 4px;
  user-select: none;
}
.title {
  font-size: 28px;
  font-weight: 400;
  color: #1f2f3d;
  text-align: left;
  user-select: text;
}
.subtitle {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
  text-align: left;
  margin: 14px 0;
  user-select: text;
}
code {
  color: #5e6d82;
  background-color: #e6effb;
  margin: 0 4px;
  display: inline-block;
  padding: 1px 5px;
  font-size: 12px;
  border-radius: 3px;
  height: 18px;
  line-height: 18px;
}
/deep/ .source {
  background: #5e5e5e;
}
</style>



