<template>
  <el-row class="submain">
    <el-col
      :span="24"
      class="title"
    >{{title}}</el-col>
    <el-col
      :span="24"
      class="subtitle"
    >{{subtitle}}</el-col>
    <el-col :span="24">
      <div
        class="container"
        @mouseenter="mouseEnter"
        @mouseleave="mouseLeave"
      >
        <div class="source">
          <slot
            name="source"
            v-if="$slots.source"
          ></slot>
        </div>
        <div
          class="meta"
          :class="{'meta-unfold':flag}"
        >
          <div
            class="description"
            v-if="$slots.description"
          >
            <slot name="description"></slot>
          </div>
          <div class="highlight">
            <pre v-highlightjs="code">
              <code class="html hljs xml"> 
              </code>
            </pre>
          </div>
        </div>
        <div
          class="demo-block-control"
          :class="{'control-unfold':flag}"
          @click="toggle"
        >
          <i
            v-show="!flag"
            class="el-icon-caret-bottom
"
          ></i>
          <i
            v-show="flag"
            class="el-icon-caret-top"
          ></i>
          <span :class="{'code-text-hover':isIn}">{{codeText}}</span>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import "highlight.js/styles/color-brewer.css";
export default {
  props: {
    title: {
      type: String
    },
    subtitle: {
      type: String
    },
    code: {
      type: String
    }
  },
  name: "LComponentNote",
  data() {
    return {
      flag: false,
      isIn: false
    };
  },
  methods: {
    toggle() {
      this.flag = !this.flag;
      console.log(this.flag);
    },
    mouseEnter() {
      console.log("enter");
      this.isIn = true;
    },
    mouseLeave() {
      console.log("leave");
      this.isIn = false;
    }
  },
  computed: {
    codeText: function() {
      return this.flag ? "隐藏代码" : "显示代码";
    }
  },
  mounted: function() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.submain {
  margin-top: 50px;
}
.title {
  font-size: 22px;
  font-weight: 400;
  color: #1f2f3d;
  text-align: left;
}
.subtitle {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
  text-align: left;
  margin: 14px 0;
}
.container {
  border: 1px solid #ebebeb;
  border-radius: 3px;
  transition: 0.2s;
  margin-bottom: 24px;
}
.container .source {
  padding: 24px;
  text-align: left;
}
.container .meta {
  background-color: #fafafa;
  border-top: 1px solid #eaeefb;
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.2s;
  text-align: left;
}
.container .meta .description {
  padding: 20px;
  box-sizing: border-box;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  font-size: 14px;
  line-height: 22px;
  color: #666;
  word-break: break-word;
  margin: 10px;
  background-color: #fff;
  user-select: text;
}
.container .meta .highlight {
  user-select: text;
}
.container .demo-block-control {
  border-top: 1px solid #eaeefb;
  height: 44px;
  line-height: 44px;
  box-sizing: border-box;
  background-color: #fff;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  text-align: center;
  margin-top: -1px;
  color: #d3dce6;
  cursor: pointer;
  position: relative;
}
.container .demo-block-control i {
  transition: 0.3s;
  transition-delay: 0.1s;
}
.container .demo-block-control span {
  position: absolute;
  font-size: 14px;
  line-height: 44px;
  transition: 0.3s;
  transition-delay: 0.1s;
  opacity: 0;
}
/* 打开后状态 */
.container .control-unfold {
  position: sticky;
  bottom: 0;
}
.container .meta-unfold {
  max-height: 1000px;
}
.hljs {
  padding: 18px 24px;
}

/* 鼠标悬浮在资源上的状态 */
.demo-block-control:hover {
  color: #409eff;
}
.container:hover .demo-block-control i {
  transform: translateX(-30px) !important;
}
.container .code-text-hover {
  opacity: 1 !important;
  transform: translateX(-30px) !important;
}
</style>



